
















export default {
	data() {
		return {
			newsinfo : {}
		};
	},
	// 页面加载成功执行函数.
	onLoad:function(e){
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.newsinfo = res.data;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
